import React from 'react';

import Draggable from './Draggable';
import Kit from './Kit';

import '../styles/Player.css';

const DRAG_MAX_LEFT = 0;
const DRAG_MAX_RIGHT = 95.0;
const DRAG_MAX_TOP = 0;
const DRAG_MAX_BOTTOM = 87.5;

class Player extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isEditing: false
    };
    this.labelInput = null;

    this.disableEditing = this.disableEditing.bind(this);
    this.enableEditing = this.enableEditing.bind(this);
    this.onLabelInputKeydown = this.onLabelInputKeydown.bind(this);
    this.movePlayer = this.movePlayer.bind(this);
    this.setLabel = this.setLabel.bind(this);
  }

  disableEditing() {
    this.setState({
      isEditing: false
    });
  }

  enableEditing() {
    this.setState({
      isEditing: true
    });
  }

  onLabelInputKeydown(event) {
    switch (event.which) {
      case 27:
        this.disableEditing();
        break;

      case 13:
        this.setLabel();
        this.disableEditing();
        break;

      default:
        break;
    }
  }

  onMouseDown(event) {
    if (this.state.isEditing) {
      this.disableEditing();
    }
  }

  setLabel() {
    if (!this.labelInput.value) { return; }
    const { actions, formIdx, lineIdx } = this.props;
    actions.changePlayerLabel({
      formIdx, lineIdx,
      label: this.labelInput.value
    });
  }

  movePlayer(pos) {
    const { actions, formIdx, lineIdx } = this.props;
    actions.movePlayer({
      formIdx, lineIdx,
      ...pos
    });
  }

  render() {
    const label = this.props.label;
    let cls = 'Player ' + label;
    cls += this.props.isGK ? ' GK' : '';

    return (
      <Draggable
        containerId="Pitch"
        maxLeft={DRAG_MAX_LEFT}
        maxRight={DRAG_MAX_RIGHT}
        maxTop={DRAG_MAX_TOP}
        maxBottom={DRAG_MAX_BOTTOM}
        left={this.props.left}
        top={this.props.top}
        onMove={this.movePlayer}
      >

        <div className={cls} title={this.props.label} >

          <div className="Player-background">
            <Kit
              shirtFill={this.props.kitColor}
              shortsFill={this.props.kitColor}
              stroke="#ffffff"
            ></Kit>
          </div>

          <div
            className="Player-label"
            style={{ color:'#ffffff' }}
            onMouseDown={(e) => {
              this.enableEditing();
              e.stopPropagation();
            }}
          >
          {this.state.isEditing
              ?
              <input
                defaultValue={this.props.label}
                ref={(labelInput) => this.labelInput = labelInput}
                type="text"
                onKeyDown={this.onLabelInputKeydown}
              />
              :
              this.props.label}
            </div>

        </div>

      </Draggable>
    );
  }
}

export default Player;



