import * as types from '../constants/actionTypes';
import { getFormation } from '../utils/formations';
import initialState from './initialState';

export default function(state = initialState, action) {
  switch (action.type) {

    case types.SET_SELECTED_FORMATION: {
      const { selectedFormation } = action;
      return {...state, selectedFormation};
    }

    case types.SET_SELECTED_KIT_COLOR: {
      const { selectedKitColor } = action;
      return {...state, selectedKitColor};
    }

    case types.CHANGE_FORMATION: {
      const formation = getFormation(action.formationType);
      return {...state, formation};
    }

    case types.CHANGE_KIT_COLOR: {
      return {...state, kitColor: action.kitColor };
    }

    case types.CHANGE_PLAYER_LABEL:{
      const { formIdx, lineIdx, label } = action;
      const formation = [...state.formation];
      const player = formation[formIdx][lineIdx];
      formation[formIdx][lineIdx] = {...player, label};
      return {...state, formation};
    }

    case types.MOVE_PLAYER:{
      const { formIdx, lineIdx, left, top } = action;
      const formation = [...state.formation];
      const player = formation[formIdx][lineIdx];
      formation[formIdx][lineIdx] = {...player, left, top};
      return {...state, formation};
    }

    default:
      return state;
  }
}



