import React from 'react';

import Player from './Player';

import '../styles/Formation.css';

export default function Formation(props) {
  const formation = props.formation || [];
  return (
    <div className="Formation">

      {formation.map((line, formIdx) => {
        return (
          <div
            className="Formation-line"
            key={formIdx}>

            {line.map((player, lineIdx) => {
              const isGK = (formIdx === 0 && lineIdx === 0);
              return (
                <Player
                  actions={props.actions}
                  formIdx={formIdx}
                  isGK={isGK}
                  key={player.label + formIdx + lineIdx}
                  kitColor={props.kitColor}
                  lineIdx={lineIdx}
                  label={player.label}
                  left={player.left}
                  top={player.top}
                ></Player>
              );
            })}

          </div>
        );
      })}

    </div>
  );
}



