import React from 'react';
import throttle from 'lodash/throttle';

import '../styles/Draggable.css';

const MOVE_THROTTLE_MS = parseInt((1000 / 60) * 2, 10);


class Draggable extends React.Component {
  constructor(props) {
    super(props);

    this.drag = {
      left: 0,
      top: 0
    };
    this.container = null;

    this.onMouseDown = this.onMouseDown.bind(this);
    this.onMouseMove = throttle(this.onMouseMove.bind(this), MOVE_THROTTLE_MS);
    this.onMouseUp = this.onMouseUp.bind(this);
  }

  calculatePosition(event) {
    const { dragElem } = this.refs;

    let left = event.pageX - (dragElem.clientWidth / 2);
    left = left / this.container.clientWidth * 100;
    left = Math.max(left, this.props.maxLeft);
    left = Math.min(left, this.props.maxRight);

    let top = event.pageY - 66;
    top -= (dragElem.clientHeight / 2);
    top = top / this.container.clientHeight * 100;
    top = Math.max(top, this.props.maxTop);
    top = Math.min(top, this.props.maxBottom);

    const pos = {
      left: left.toFixed(2) + '%',
      top:  top.toFixed(2) + '%'
    };
    return pos;
  }

  onMouseDown(event) {
    this.container = document.getElementById(this.props.containerId);
    this.drag = this.calculatePosition(event);
    this.container.addEventListener('mouseup', this.onMouseUp);
    this.container.addEventListener('mousemove', this.onMouseMove);
  }

  onMouseMove(event) {
    this.drag = this.calculatePosition(event);
    this.props.onMove({
      left: this.drag.left,
      top: this.drag.top
    });
  }

  onMouseUp(event) {
    this.drag = this.calculatePosition(event);
    this.props.onMove({
      left: this.drag.left,
      top: this.drag.top
    });
    this.container.removeEventListener('mouseup', this.onMouseUp);
    this.container.removeEventListener('mousemove', this.onMouseMove);
    this.container = null;
  }

  render() {
    const style = {
      position: 'absolute',
      top: this.props.top,
      left: this.props.left
    };
    return(
      <div
        className="Draggable"
        ref="dragElem"
        style={style}
        onMouseDown={this.onMouseDown}
      >
        {this.props.children}
      </div>

    );
  }

}

export default Draggable;



