import React from 'react';

import '../styles/Kit.css';

export default Kit;

const shortsPath = [
  'M 31.218752,48 L 68.781331,48 L 74.500103,88.5 L 53.500059,88.5',
  'L 50.500053,75 L 47.500046,88.5 L 25.5,88.5 L 31.218752,48 z'
].join(' ');

const shirtPath = [
  'M -0.5,58.5 L -0.5,31.5 L -6,37 L -19,24 L -0.5,5.5 L 9.656,5.5',
  'C 14.359984,7.5600799 16.72203,8.5842032 19.074651,8.5812448',
  'C 21.427273,8.5782865 23.770718,7.5482466 28.469,5.5',
  'L 38.5,5.5 L 57,24 L 44,37 L 38.5,31.5 L 38.5,58.5 L -0.5,58.5'
].join(' ');

function Kit(props) {
  return (
    <svg className="Kit" version="1.0" width="70" height="80" viewBox="0 0 100 90">

      <path className="shorts" fill={props.shortsFill} stroke={props.stroke} d={shortsPath} />

      <g transform="translate(31,-5)">
        <path className="shirt" fill={props.shirtFill} stroke={props.stroke} d={shirtPath} />
      </g>

    </svg>
  );
}



