import React from 'react';

import Header from '../containers/Header';
import Main from '../components/Main';
import '../styles/App.css';

class App extends React.Component {
  render() {
    return (
      <div className="App">
        <Header></Header>
        <Main></Main>
      </div>
    );
  }
}

export default App;



